.heroButton {
    border-radius: 12px;
    padding: 18px 30px;

    font-family: Roboto, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    text-transform: uppercase;

    transition: all 0.2s ease-in-out;
}

.heroButton:hover {
    transform: scale(0.97);
}